import { _decorator, size, director, Node } from "cc";
const { ccclass, property } = _decorator;

import BaseScene from "../BaseObject/BaseScene";
import AssetManager from "../DataManager/AssetManager";
import SlotManager from "../Prefabs/SlotManager";

@ccclass("MainScene")
export class MainScene extends BaseScene {
  assetManager: AssetManager = null;

  @property(Node)
  slotButton: Node | null = null;
  @property(Node)
  diceButton: Node | null = null;

  async onLoad() {
    super.onLoad();
    this.assetManager = AssetManager.getInstance();
    const assetResult = await this.assetManager.loadAssets();
    this.slotButton.active = true;
    this.diceButton.active = true;
  }

  slotClicked() {
    director.loadScene("SlotScene");
  }

  diceClicked() {
    director.loadScene("DiceScene");
  }
}
